import modal from './modal';
import scroll from './scroll';
import form from './form';
import valid from './valid';


AOS.init({
  disable: 'phone'
});


if (document.body.getAttribute('data-title') === 'index') {

  $('.slider__wrapper').slick({
    slidesToShow: 3,
    prevArrow: '<button class="slider__btn-left"><img src="images/slider-left.svg" alt=""></button>',
    nextArrow: '<button class="slider__btn-right"><img src="images/slider-right.svg" alt=""></button>'
  })


  modal('.header__back-phone', '.popup', '.popup__content', '.popup__close-btn');
  scroll();
  form('.contact__form', '.contact__form-name', '.contact__form-phone', '.contact__form-submit', '.contact__form-status');
  form('.popup__form', '.popup__name', '.popup__phone', '.popup__submit', '.popup__status');
  valid('.popup__name','.popup__phone');
  valid('.contact__form-name','.contact__form-phone');
}

if (document.body.getAttribute('data-title') === 'calc') {
  const range = () => {
    const track = document.querySelector('.calc__square-range');
    const trackWrapper = document.querySelector('.range__track');
    const rangeValue = document.querySelector('.range__value');
    const progress = document.querySelector('.range__progress');

    track.addEventListener('input', () => {
      trackWrapper.style.left = track.value + 'px';
      trackWrapper.style.transform = 'translateX(-50%)';
      rangeValue.textContent = track.value;
      progress.style.width = track.value + 'px';
    })
  }
  range()

  const calc = () => {

    const wrapper = document.querySelector('.calc__wrapper');

    wrapper.addEventListener('click', e => {

      let target = e.target;
      if (target.classList.contains('btn')) {
        e.preventDefault();
        const item = target.closest('.calc__item');
        const id = target.getAttribute('href');
        const nextItem = document.querySelector(id);
        item.classList.toggle('calc__item--active');
        document.querySelector(id).classList.toggle('calc__item--active');
      }
    })

  }
  calc();

  form('.calc__form', '.calc__name', '.calc__input-phone', '.calc__submit', '.calc__status');
  valid('.calc__name','.calc__input-phone');

  $(".calc__square-range").ionRangeSlider({
    min: 0,
    max: 1000
  });
}

