const scroll = () => {
	const scrollLink = document.querySelectorAll('.scroll-link');

	scrollLink.forEach(item => {
		item.addEventListener('click', e => {
			e.preventDefault();
			const id = item.getAttribute('href');
			const elem = document.querySelector(id)
			elem.scrollIntoView({
				top: elem.offsetTop,
				behavior: 'smooth'
			});
		})
	})

};


export default scroll;