const sendFormData = (formId, name, phone, submitId, statusId) => {

  const form = document.querySelector(formId);
  const status = document.querySelector(statusId);
  const submit = document.querySelector(submitId);
  const formName = document.querySelector(name);
  const formPhone = document.querySelector(phone);


  const message = {
    loading: 'Загрузка...',
    succes: 'Готово',
    failure: 'Ошибка'
  };


  const postData = async (data) => {
    status.style.display = 'block';
    status.textContent = message.loading;
    let res = await fetch('tg.php', {
      method: 'POST',
      body: data
    });
    return await res.text();
  };

  const send = () => {
    let formData;
    if (form === document.querySelector('.popup__form')) formData = new FormData(document.querySelector('.popup__form'));
    else if (form === document.querySelector('.calc__form')) formData = new FormData(document.querySelector('.calc__form'));
    else formData = new FormData(form);
    postData(formData)
      .then(res => {
        if (res) {
          showStatus(message.failure);
        } else {
          showStatus(message.succes);
          form.reset();
        }
      })
      .catch(() => { showStatus(message.failure) })
  };

  const showStatus = text => {
    submit.setAttribute('disabled', 'disabled');
    if(form === document.querySelector('.calc__form')){
      submit.textContent = text;
    } else  status.textContent = text;
    setTimeout(() => {
      submit.removeAttribute('disabled');
      if(form === document.querySelector('.calc__form')){
        submit.textContent = 'Отправить'
      } else status.textContent = '';

    }, 2000);
  };

  form.addEventListener('submit', e => {
    e.preventDefault();
    if (!formName.value.trim()) {
      showStatus('Введите имя');
    } else if (!formPhone.value.trim()) {
      showStatus('Введите телефон');
    } else send();
  });

};

export default sendFormData;

