const modal = (button, over, popup, close) => {
  const btn = document.querySelector(button);
  const overlay = document.querySelector(over);
  const content = document.querySelector(popup);
  const closeBtn = document.querySelector(close);
  
  let padding = innerWidth - document.body.offsetWidth + 'px';
  btn.addEventListener('click', (e) => {
    e.preventDefault();
    overlay.classList.toggle('popup__open');
    content.style.height = content.scrollHeight + 'px';
    document.body.style.paddingRight = padding;
    document.body.style.overflow = 'hidden';
    content.style.marginLeft = 0;
  });

  overlay.addEventListener('click', e => {
    const target = e.target;
    if (target === overlay || target.classList.contains('popup__close-btn')) {
      overlay.classList.toggle('popup__open');
      document.body.style.overflow = '';
      document.body.style.paddingRight = 0;
      content.style.height = 0;
      content.style.marginLeft = padding;
    } 
  })

};

export default modal;